import React from 'react';
import { Button, Modal, Form, InputGroup } from 'react-bootstrap';
import "../../../../../assets/scss/plugins/forms/react-select/_react-select.scss";
import { Search } from 'react-feather';


class GeneralSettingsModal extends React.Component {
    render() {
        return (
            <div>
                <Modal
                    show={this.props.show}
                    onHide={this.props.handleHide}
                >
                    <Modal.Header closeButton>
                        <Modal.Title id="AddUpdate">
                            {this.props.InsertUpdate} GeneralSettings
                        </Modal.Title>
                    </Modal.Header>

                    <Form noValidate validated={this.props.validated} onSubmit={this.props.handleSubmit} autoComplete="off">
                        <Modal.Body>
                            <Form.Group controlId="KEYVALUE">
                                <Form.Label>Key Value</Form.Label>
                                <Form.Control type="text" placeholder="Enter Key Value"
                                    value={this.props.KEYVALUE} required
                                    onChange={this.props.changeKeyValue} name="KeyValue" />
                                <Form.Control.Feedback type="invalid">
                                    Please select Key Value.
                                 </Form.Control.Feedback>
                            </Form.Group>
                            <Form.Group controlId="TextValue">
                                <Form.Label>Text Value</Form.Label>
                                <Form.Control type="text" placeholder="Enter Text Value"
                                    value={this.props.TEXTVALUE} required
                                    onChange={this.props.changeTextValue} name="TextValue" />
                                <Form.Control.Feedback type="invalid">
                                    Please select Text Value.
                                 </Form.Control.Feedback>
                            </Form.Group>
                            <Form.Group controlId="Names">
                                <Form.Label>Names</Form.Label>
                                <Form.Control type="text" placeholder="Enter Names"
                                    value={this.props.NAMES} required
                                    onChange={this.props.changeNames} name="Names" />
                                <Form.Control.Feedback type="invalid">
                                    Please select Names.
                                 </Form.Control.Feedback>
                            </Form.Group>
                            <Form.Group controlId="SystemStatusType">
                                <Form.Label>System Status Type</Form.Label>
                                <InputGroup>
                                    <Form.Control
                                        type="text"
                                        name="SystemStatus"
                                        value={this.props.SystemStatusTypeDescription}
                                        placeholder="Search System Status Type"
                                        aria-describedby="inputGroupPrepend"
                                        required
                                    />
                                    <InputGroup.Prepend>
                                        <Button variant="info" size="sm" onClick={this.props.handleShowSearch}>
                                            <Search />
                                        </Button>
                                    </InputGroup.Prepend>
                                    <Form.Control.Feedback type="invalid">
                                        Please select status.
                                 </Form.Control.Feedback>
                                </InputGroup>
                            </Form.Group>
                        </Modal.Body>

                        <Modal.Footer>
                            <Button variant="success" onClick={this.props.handleHide}>
                                Cancel
                            </Button>
                            <Button variant="primary" type="submit" disabled={this.props.isEnabled}>
                                {this.props.InsertUpdate}
                            </Button>
                        </Modal.Footer>
                    </Form>
                </Modal>

                <Modal
                    show={this.props.showdelete}
                    onHide={this.props.handleHideDelete}
                >
                    <Modal.Header closeButton>
                        <Modal.Title id="AddUpdate">
                            Delete General Settings
                        </Modal.Title>
                    </Modal.Header>
                    <Form onSubmit={this.props.handleDelete} autoComplete="off">
                        <Modal.Body>
                            <Form.Group controlId="description">
                                <h4>Are you sure you want to delete this record?</h4>
                                <Form.Control type="text" placeholder="Click Delete Button" value={this.props.KEYVALUE} onChange={this.props.KEYVALUE} readOnly />
                            </Form.Group>
                        </Modal.Body>

                        <Modal.Footer>
                            <Button variant="success" onClick={this.props.handleHideDelete}>
                                Cancel
                            </Button>
                            <Button variant="primary" type="submit">
                                Yes Delete
                            </Button>
                        </Modal.Footer>
                    </Form>
                </Modal>
            </div>
        );
    }
}

export default GeneralSettingsModal;